.people .list {
  display: -ms-grid;
  display: grid;
  -webkit-box-align: start;
  -ms-flex-align: start;
  align-items: start;
	-ms-grid-columns: (1fr);
	grid-template-columns: repeat(1, 1fr);
	gap: 20px 12px;
	overflow-x: auto;
  position: relative;
  z-index: 2;
}

@media only screen and (min-width: 580px) {
  .people .list {
    -ms-grid-columns: (1fr);
		grid-template-columns: repeat(2, 1fr);
	}
}

@media only screen and (min-width: 1050px) {
  .people .list {
    -ms-grid-columns: (1fr);
    grid-template-columns: repeat(4, 1fr);
    margin-bottom: 2rem;
  }
}

.people .el {
  margin: 0;
  padding: 32px 16px 40px 16px;
  position: relative;
  background: #232323;
  color: var(--grey);
  font-size: 15px;
  line-height: 1.6;
  border-radius: 10px;
  box-shadow: var(--box-shadow);
  text-align: left;
  display: block;
  border: 0;
  outline: none;
  -webkit-transition: opacity 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
  transition: opacity 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
}

.people .el.fadeMe {
  opacity: 0;
}

.people .el.folded:not(.show) {
  display: none;
}

.people .el .photo {
  position: relative;
  margin: 0 auto 20px auto;
  width: 155px;
  height: 155px;
  border-radius: 50%;
}

.people .el .photo:before {
  content: '';
  position: absolute;
  top: 36%;
  left: 37%;
  background: url(../img/partners/Frame.png) no-repeat 50% 50%;
  width: 212px;
  height: 215px;
  background-size: contain;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  margin: -85px -85.5px;
  -webkit-animation: rotate 10s linear infinite;
  animation: rotate 10s linear infinite;
  -webkit-animation-play-state: paused;
  animation-play-state: paused;
}

@media (pointer:fine) {
  .people .el:hover .photo:before {
    -webkit-animation-play-state: running;
    animation-play-state: running;
  }
}

@-webkit-keyframes rotate {
  from {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@keyframes rotate {
  from {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

.people .el .photo>img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  -o-object-fit: cover;
  object-fit: cover;
  -o-object-position: center;
  object-position: center;
}

.people .el .photo .social {
  position: absolute;
  bottom: 0;
  right: 0;
  margin: -13px;

  & > svg {
    height: 30px;
    width: 30px;
  }
}

.people .el .photo .social img {
  width: 32px;
  height: auto;
}

.people .el .caption {
  color: var(--white);
  text-align: center;
  font-size: 18px;
  line-height: 1.666;
  text-transform: uppercase;
  margin: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  min-height: 5em;
}

.people .el h3,
.people .el .h3 {
  font-size: 133%;
  line-height: 1.25;
  overflow: hidden;
  max-height: 2.5em;
  margin: 0;
  color: var(--white);
}

.people .el .descr {
  overflow: hidden;
  height: 4.8em;
  color: #acacac;
}

.people .el .link-more {
  white-space: nowrap;
  position: absolute;
  bottom: 0;
  right: 0;
  margin: 18px;
  font-size: 12px;
  text-decoration: none;
  color: #acacac;

  &:hover {
    cursor: pointer;
  }
}

.people .el:not(.hasMore) .link-more {
  display: none;
}

.people .el:not(.open) .link-more .for-close,
.people .el.open .link-more .for-open {
  display: none;
}

.people .load-more {
  text-align: center;
  margin-top: 30px;
  /*02.12.21*/
}

.people:not(.hasFolded) .load-more {
  display: none;
}

.people .load-more .btn {
  display: inline-block;
  vertical-align: top;
  min-width: 170px;
}